'use client'
import { useState } from "react"
import axios from "axios";
import Link from "next/link";

export default function RegisterPage(){
    const [form, setForm] = useState({
        name: "",
        email: "",
        password: ""
    });

    async function handleSubmit(e){
        e.preventDefault();
        try{
            const res = await axios.post('/api/register', form);
             alert(res.data.message);
        }catch(err){
            console.error(err);
        }
    }

    return(
        <>
        <div className="register-page flex flex-col gap-4 m-10 ">
        <form  onSubmit={handleSubmit}className="flex flex-col gap-2">

            <div className="flex gap-3">
            <label htmlFor="name">Name</label>
            <input className="border border-gray-300 p-2 rounded" placeholder="name"  value={form.name} onChange={(e) => setForm({...form, name: e.target.value})} />

            </div>

            <div className="flex gap-3">
            <label htmlFor="email">Email</label>
            <input className="border border-gray-300 p-2 rounded" placeholder="email"  value={form.email} onChange={(e) => setForm({...form, email: e.target.value})} />

            </div>

            <div className="flex gap-3">
                <label htmlFor="password">Password</label>
                <input className="border border-gray-300 p-2 rounded" placeholder="password"  value={form.password} onChange={(e) => setForm({...form, password: e.target.value})} />

            </div>

            <div className="flex w-1/3 justify-between">
            <button className="bg-blue-500 text-white p-2 rounded w-fit " type="submit">Register</button>
            <Link href="/login" className="bg-blue-500 text-white p-2 rounded w-fit ">Login</Link>
                
            </div>

        </form>

        </div>
        </>
    )
}